body {
  background-color: #282c34;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
}

.App-header {
  min-height: 10vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #fafafa;
  padding: 0 0 2rem 0;
}

.App-joke {
  background-color: #bfbfbf;
  min-height: 50vh;
  padding: 2rem;
  font-size: calc(10px + 2vmin);
  color: #000;
}

.App-joke p {
  background: #fafafa;
  color: #000;
  padding: 2rem;
  max-width: 30rem;
  margin: 0 auto;
  border-radius: 4px;
  line-height: 1.5;
  transition: all 200ms;
  box-shadow: 0 0 0 5px (0, 0, 0, 0.7);
}

.App-link {
  background: #61dafb;
  padding: 1rem 2rem;
  color: #000;
  text-decoration: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
}

.App-footer {
  color: #ccc;
  font-weight: 500;
  min-height: 10vh;
  padding: 1rem 0;
  text-align: center;
}
